import React from 'react';

export default function StageView({ stagedItems, setStagedItems }) {
  function handleCountChange(idx, value) {
    const newItems = [...stagedItems];
    newItems[idx].askCount = parseInt(value, 10) || 0;
    setStagedItems(newItems);
  }

  function handlePriceChange(idx, value) {
    const newItems = [...stagedItems];
    // Remove any non-numeric except dot
    let sanitizedValue = value.replace(/[^0-9.]/g, '');
    newItems[idx].unitPrice = sanitizedValue;
    setStagedItems(newItems);
  }

  return (
    <div className="table-responsive">
      <table className="table table-striped table-hover align-middle">
        <thead>
          <tr>
            <th>Section</th>
            <th>Product</th>
            <th>Price ($)</th>
            <th>Quantity</th>
          </tr>
        </thead>
        <tbody>
          {stagedItems.map((item, idx) => {
            const priceValue =
              item.unitPrice !== undefined && item.unitPrice !== null
                ? String(item.unitPrice).replace(/[^0-9.]/g, '')
                : '';
            return (
              <tr key={idx}>
                <td>{item.section}</td>
                <td>{item.product}</td>
                <td>
                  <div className="input-group input-group-sm">
                    <span className="input-group-text">$</span>
                    <input
                      type="number"
                      inputMode="decimal"
                      className="form-control form-control-sm"
                      value={priceValue}
                      onChange={(e) => handlePriceChange(idx, e.target.value)}
                      style={{ maxWidth: '100px' }}
                      min="0"
                      step="0.01"
                    />
                  </div>
                </td>
                <td>
                  <input
                    type="number"
                    className="form-control form-control-sm"
                    min="0"
                    value={item.askCount}
                    onChange={(e) => handleCountChange(idx, e.target.value)}
                    style={{ maxWidth: '70px' }}
                  />
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}
