import React, { useState, useEffect } from 'react';
import Papa from 'papaparse';
import InventoryTable from './InventoryTable';
import StageView from './StageView';
import InvoiceView from './InvoiceView';
import { arrayToCsv } from './utils';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

const STORAGE_KEY = 'inventory-data-v1';
const basePath = process.env.NODE_ENV === 'production' ? process.env.PUBLIC_URL : '';

// Update version here as needed
const APP_VERSION = "1.0.2";

function fetchCSV(url) {
  return fetch(url)
    .then((res) => res.text())
    .then((text) => {
      const result = Papa.parse(text, {
        header: true,
        skipEmptyLines: true,
        quoteChar: '"',
        escapeChar: '"',
      });
      return result.data.map((row) => ({
        section: row.Section || '',
        product: row.Product || '',
        unitPrice: row['Unit Price']
          ? String(row['Unit Price']).replace(/^\$/, '')
          : "0",
        count: parseInt(row.Count, 10) || 0,
        description: row.Description || '',
        selected: false,
        askCount: 0,
      }));
    })
    .catch(() => []);
}

export default function App() {
  const [inventory, setInventory] = useState([]);
  const [stagedItems, setStagedItems] = useState([]);
  const [discount, setDiscount] = useState(10);

  useEffect(() => {
    const storedInventory = localStorage.getItem(STORAGE_KEY);
    if (storedInventory) {
      setInventory(JSON.parse(storedInventory));
    } else {
      fetchCSV(`${basePath}/Inventory.csv`).then((data) => setInventory(data));
    }
  }, []);

  useEffect(() => {
    if (inventory.length > 0) {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(inventory));
    }
  }, [inventory]);

  function generateStage() {
    const selectedItems = inventory.filter((item) => item.selected);
    setStagedItems(selectedItems.map((item) => ({ ...item, askCount: 0 })));
  }

  function exportInvoice() {
    const filteredItems = stagedItems
      .filter((item) => item.askCount > 0)
      .map((item) => ({
        Section: item.section,
        Product: item.product,
        Description: item.description,
        Price: item.unitPrice,
        Quantity: item.askCount,
        Subtotal: (item.askCount * parseFloat(item.unitPrice)).toFixed(2),
      }));
    const csv = arrayToCsv(filteredItems);
    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'invoice.csv';
    document.body.appendChild(link);
    link.click();
    link.remove();
    URL.revokeObjectURL(url);
  }

  function exportInventory() {
    const csv = arrayToCsv(inventory);
    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'inventory.csv';
    document.body.appendChild(link);
    link.click();
    link.remove();
    URL.revokeObjectURL(url);
  }

  function resetInventory() {
    localStorage.removeItem(STORAGE_KEY);
    fetchCSV(`${basePath}/Inventory.csv`).then((data) => setInventory(data));
  }

  function resetSelected() {
    const newInventory = inventory.map((item) => ({ ...item, selected: false }));
    setInventory(newInventory);
  }

  return (
    <div className="container my-4">
      <div className="mb-3 text-muted" style={{ fontSize: '0.9rem', fontWeight: 'bold' }}>
        App Version: v{APP_VERSION}
      </div>

      <h1 className="mb-4">Inventory Manager Web App</h1>
      <p>Inventory count: {inventory.length}</p>
      {inventory.length === 0 && <p>Loading inventory...</p>}
      {inventory.length > 0 && (
        <>
          <InventoryTable inventory={inventory} setInventory={setInventory} />
          <div className="my-3 d-flex flex-wrap gap-2 justify-content-start">
            <button className="btn btn-primary flex-grow-1 flex-md-grow-0" onClick={generateStage}>
              Generate Stage
            </button>
            <button className="btn btn-secondary flex-grow-1 flex-md-grow-0" onClick={exportInventory}>
              Export Inventory CSV
            </button>
            <button className="btn btn-warning flex-grow-1 flex-md-grow-0" onClick={resetInventory}>
              Reset Inventory From CSV
            </button>
            <button className="btn btn-info flex-grow-1 flex-md-grow-0" onClick={resetSelected}>
              Reset Selected Items
            </button>
          </div>
          <div className="my-3">
            <label htmlFor="discountInput" className="form-label">
              Discount (%)
            </label>
            <input
              type="number"
              id="discountInput"
              className="form-control form-control-sm"
              min="0"
              max="100"
              value={discount}
              onChange={(e) => setDiscount(e.target.value)}
              style={{ maxWidth: '140px' }}
              inputMode="decimal"
            />
          </div>
          {stagedItems.length > 0 && (
            <>
              <StageView stagedItems={stagedItems} setStagedItems={setStagedItems} />
              <button className="btn btn-success mt-3" onClick={exportInvoice}>Export Invoice CSV</button>
              <InvoiceView stagedItems={stagedItems} discount={discount} />
            </>
          )}
        </>
      )}
    </div>
  );
}
