import React, { useState, useEffect } from 'react';

function ceilToNextFive(amount) {
  return Math.ceil(amount / 5) * 5;
}

export default function InvoiceView({ stagedItems, discount = 10 }) {
  const [manualTotal, setManualTotal] = useState('');

  // Sanitize and parse function for prices removing $ and invalid chars
  function sanitizePrice(price) {
    if (typeof price !== 'string') return price || 0;
    const cleaned = price.replace(/[^0-9.]/g, '');
    return cleaned === '' ? 0 : parseFloat(cleaned);
  }

  // Compute total by summing sanitized and parsed prices
  const total = stagedItems.reduce(
    (sum, item) =>
      sum +
      (sanitizePrice(item.unitPrice) * (item.askCount || 0)),
    0
  );

  const discountFraction = 1 - (parseFloat(discount) || 0) / 100;
  const computedDiscountTotal = ceilToNextFive(total * discountFraction);

  useEffect(() => {
    setManualTotal('');
  }, [total, discount, stagedItems]);

  // Create invoice rows with sanitized prices and formatted strings
  const invoiceRows = stagedItems
    .filter((item) => item.askCount > 0)
    .map((item) => {
      const priceNum = sanitizePrice(item.unitPrice);
      return {
        product: item.product,
        unitPrice: priceNum.toFixed(2),
        quantity: item.askCount,
        subtotal: (priceNum * item.askCount).toFixed(2),
      };
    });

  return (
    <div className="table-responsive">
      <table className="table table-bordered text-center align-middle">
        <thead>
          <tr style={{ background: '#cfcfcf' }}>
            <th colSpan={2}>Total</th>
            <th colSpan={2}>Final Total After Discounts</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td colSpan={2}><b>${total.toFixed(2)}</b></td>
            <td colSpan={2}>
              <div
                className="input-group input-group-sm justify-content-center"
                style={{ maxWidth: '140px', margin: 'auto' }}
              >
                <span className="input-group-text">$</span>
                <input
                  type="number"
                  className="form-control form-control-sm text-end"
                  value={manualTotal === '' ? computedDiscountTotal : manualTotal}
                  onChange={(e) => setManualTotal(e.target.value)}
                  style={{ maxWidth: '100px' }}
                  min="0"
                  placeholder={computedDiscountTotal}
                />
              </div>
            </td>
          </tr>
        </tbody>

        <thead>
          <tr style={{ background: '#cfcfcf' }}>
            <th>Product</th>
            <th>Price</th>
            <th>Qt</th>
            <th>Subtotal</th>
          </tr>
        </thead>
        <tbody>
          {invoiceRows.map((row, idx) => (
            <tr key={idx}>
              <td>{row.product}</td>
              <td>${row.unitPrice}</td>
              <td>{row.quantity}</td>
              <td>${row.subtotal}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
